let windowWidth = 0, scrollTimer = false,tabBar;		
export default {
	data() {
		return {
			orderType:1,
			tabList:[{id:1,name:'全部订单'},{id:2,name:'待付款'},{id:3,name:'正在砍'},{id:4,name:'未砍中'},{id:5,name:'已砍中'}],
			tabCurrentIndex:0,
			scrollLeft:0,
			isEmpty:false,
			hasFlag:false
		};
	},
	methods:{
		changeType(id){
			if(id != this.orderType){
				this.orderType = id 
				this.tabCurrentIndex = 0
				if(id == 1){
					this.tabList = [{id:1,name:'全部订单'},{id:2,name:'待付款'},{id:3,name:'正在砍'},{id:4,name:'未砍中'},{id:5,name:'已砍中'}]
				}else{
					this.tabList = [{id:1,name:'全部订单'},{id:2,name:'待付款'},{id:3,name:'待发货'},{id:4,name:'待收货'},{id:5,name:'待评价'},{id:6,name:'已完成'}]
				}
			}
		},
		// tab切换
		async changeTab(e){
			if(scrollTimer){
				//多次切换只执行最后一次
				clearTimeout(scrollTimer);
				scrollTimer = false;
			}
			let index = e;
			//e=number为点击切换，e=object为swiper滑动切换
			if(typeof e === 'object'){
				index = e.detail.current
			}
			if(typeof tabBar !== 'object'){
				tabBar = await this.getElSize("nav-bar")
			}
			//计算宽度相关
			let tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0; 
			let nowWidth = 0;
			//获取可滑动总宽度
			for (let i = 0; i <= index; i++) {
				let result = await this.getElSize('tab' + i);
				width += result.width;
				if(i === index){
					nowWidth = result.width;
				}
			}
			if(typeof e === 'number'){
				//点击切换时先切换再滚动tabbar，避免同时切换视觉错位
				this.tabCurrentIndex = index; 
			}
			scrollTimer = setTimeout(() =>{
				if (width - nowWidth/2 > windowWidth / 2) {
					//如果当前项越过中心点，将其放在屏幕中心
					this.scrollLeft = width - nowWidth/2 - windowWidth / 2;
					console.log(this.scrollLeft);
				}else{
					this.scrollLeft = 0;
				}
				if(typeof e === 'object'){
					this.tabCurrentIndex = index; 
				}
				this.tabCurrentIndex = index; 
			},300)
		},
		//获得元素的size
		getElSize(id) { 
			return new Promise((res, rej) => {
				let el = uni.createSelectorQuery().select('#' + id);
				el.fields({
					size: true,
					scrollOffset: true,
					rect: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		handleOpenOrderDetail(){
			uni.navigateTo({
				url:'orderDetail'
			})
		}
	},
	onLoad() {
		// 获取屏幕宽度
		windowWidth = uni.getSystemInfoSync().windowWidth;
		setTimeout(()=>{
			this.isEmpty = true
		},2000)
	}
}
